<template>
	<view>
		<button @click="getUserProfile()">微信登录</button>
		
		<!-- colorui -->
		<view class="cu-card case" :class="isCard?'no-card':''">
			<view class="cu-item shadow">
				<view class="image">
					<image src="https://ossweb-img.qq.com/images/lol/web201310/skin/big10006.jpg"
					 mode="widthFix"></image>
					<view class="cu-tag bg-blue">史诗</view>
					<view class="cu-bar bg-shadeBottom"> <text class="text-cut">我已天理为凭，踏入这片荒芜，不再受凡人的枷锁遏制。我已天理为凭，踏入这片荒芜，不再受凡人的枷锁遏制。</text></view>
				</view>
				<view class="cu-list menu-avatar">
					<view class="cu-item">
						<view class="cu-avatar round lg" style="background-image:url(https://ossweb-img.qq.com/images/lol/web201310/skin/big10006.jpg);"></view>
						<view class="content flex-sub">
							<view class="text-grey">正义天使 凯尔</view>
							<view class="text-gray text-sm flex justify-between">
								十天前
								<view class="text-gray text-sm">
									<text class="cuIcon-attentionfill margin-lr-xs"></text> 10
									<text class="cuIcon-appreciatefill margin-lr-xs"></text> 20
									<text class="cuIcon-messagefill margin-lr-xs"></text> 30
								</view>
							</view>
						</view>
					</view>
				</view>
			</view>
		</view>
		
	</view>
</template>

<script>
	import api from "../../utils/api.js"
	export default {
		data() {
			return {
				userInfo: {}
			}
		},
		methods: {
			getUserProfile(e) {
				let that = this
				wx.getUserProfile({
					desc: '用于完善会员资料', // 声明获取用户个人信息后的用途，后续会展示在弹窗中，请谨慎填写
					success: (res) => {
						console.log(res)
						that.userInfo = res.userInfo
						that.bindGetUserInfo()
					}
				})
			
			},
			bindGetUserInfo(e) {
				let that = this
				wx.login({
					success(res) {
						console.log(res.code)//获得token参数
						uni.showLoading({
							title: "登录中"
						})
						//api请求
						api.login(res.code,that.userInfo.avatarUrl,that.userInfo.nickName,that.userInfo.gender).then((data) => {
							console.log(data)
							uni.hideLoading()
							//将用户信息和token存在本地
							uni.setStorageSync("user", data.data.data)
							uni.setStorageSync('token', data.data.data.token)
						})
					}
				})
			},
		}
	}
</script>

<style lang="scss">
	button {
		margin-top: 50rpx;
		background-color: #007AFF;
	}
</style>
